<template>
  <div class="search-history">
    <van-cell title="历史记录"  >

    <div v-if="isDeleteShow">
      <span style="margin-right:10px;" @click="$emit('clear-history')">全部删除</span>
      <span @click="isDeleteShow=false">完成</span>
    </div>

      <van-icon name="delete" v-else @click="isDeleteShow=true"></van-icon>
    </van-cell>

    <van-cell :title="item" v-for="(item,index) in searchHistorys" :key="index" @click="onSearchItemClick(item,index)">
      <van-icon name="close" v-if="isDeleteShow" ></van-icon>
    </van-cell>
  </div>
</template>

<script>
export default {
  name: 'SearchHistory',

  data () {
    return {
      isDeleteShow: false
    }
  },

  props: {
    searchHistorys: {
      type: Array,
      required: true
    }
  },

  mounted () {

  },

  methods: {
    // 当历史记录项被点击
    onSearchItemClick (item, index) {
      // 判断是否为删除状态
      if (this.isDeleteShow) {
        // 删除状态
        // eslint-disable-next-line vue/no-mutating-props
        this.searchHistorys.splice(index, 1)
      } else {
        this.$emit('search', item)
      }
    }
  }
}
</script>

