<template>
  <div class="search-container">

    <!-- 搜索页面 -->

    <form action="/">
      <van-search @focus="isResultShow=false" background="#3296fa" v-model="searchText" show-action placeholder="请输入搜索关键词" @search="onSearch" @cancel="onCancel" class="search-bar" />
    </form>

    <!-- 搜索结果 -->
    <search-result v-if="isResultShow" :searchText='searchText'/>
    <!-- /搜索结果 -->

    <!-- 搜索建议 -->
    <search-suggestion v-else-if="searchText" :searchText='searchText' @search="onSearch"/>
    <!-- /搜索建议 -->

    <!-- 搜索历史 -->
    <search-history v-else :searchHistorys='searchHistorys' @search="onSearch" @search-historys="searchHistory=[]"/>
    <!-- /搜索历史 -->

  </div>
</template>

<script>
import searchHistory from '@/views/search/component/search-history.vue'
import searchSuggestion from '@/views/search/component/search-suggestion.vue'
import searchResult from '@/views/search/component/search-result.vue'

import { getItem, setItem } from '@/utils/storage.js'
export default {
  name: 'SearchIndex',

  components: {
    searchHistory,
    searchSuggestion,
    searchResult
  },

  data () {
    return {
      searchText: '',
      // 是否显示搜索结果
      isResultShow: false,

      // 历史记录的数组
      searchHistorys: getItem('TOUTIAO_SEARCH_HISTORY') || []
    }
  },

  activated () {
    this.isResultShow = false
  },

  mounted () {},

  watch: {

    // 历史记录数组发生变化，则存储到本地存储

    searchHistorys (val) {
      console.log(val)
      setItem('TOUTIAO_SEARCH_HISTORY', val)
    }
  },

  methods: {
    onSearch (val) {
      // 显示搜索结果
      this.searchText = val

      // 将搜索存储未历史记录，无重复，新的排在前面
      const index = this.searchHistorys.indexOf(val)

      if (index !== -1) {
        // 去除原来的旧数据
        this.searchHistorys.splice(index, 1)
      }
      this.searchHistorys.unshift(val)
      this.isResultShow = true
    },

    // 取消按钮被点击
    onCancel () {
      this.$router.back()
    }
  }
}
</script>

<style lang="less" scoped>
.search-container {

  padding-top: 108px;
  // 取消按钮颜色
  .van-search__action {
    color: #fff;
  }

  .search-bar{
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    z-index: 1;
  }

}
</style>
