<template>
  <div class="search-suggestion">
    <van-cell  icon="search" v-for="(text,index) in suggestions" :key="index" @click="$emit('search',text)">
      <!-- title插槽，独立出来，为了文字高亮 -->
      <div slot="title" v-html="hightText(text)"></div>
    </van-cell>

  </div>
</template>

<script>
import { getSearchSuggestion } from '@/api/search.js'

// 导入lodash的防抖函数
import { debounce } from 'lodash'
export default {
  name: 'SearchSuggestion',

  data () {
    return {
      suggestions: []
    }
  },

  props: {
    searchText: {
      type: String,
      required: true
    }
  },

  filters: {

  },

  watch: {
    searchText: {

      // 使用lodash防抖函数

      handler: debounce(function (val) {
        console.log(val)
        this.loadSearchSuggestion(val)
      }, 500),
      // handler (val) {
      //   console.log(val)
      //   this.loadSearchSuggestion(val)
      // },
      immediate: true
    }
  },

  mounted () {

  },

  created () {

  },

  methods: {
    // 加载搜索建议
    async loadSearchSuggestion (q) {
      try {
        const { data: res } = await getSearchSuggestion(q)
        console.log(res)
        if (res.data.options[0] !== null) {
          this.suggestions = res.data.options
        } else {
          this.suggestions = []
        }
      } catch (err) {
        this.$toast('获取联想建议失败')
      }
    },

    // 高亮搜索的文本
    hightText (text) {
      // console.log(source)
      // console.log(this.searchText)
      const source = this.searchText

      // console.log('test', text)
      // console.log(source)

      // // 被替换之后的关键字
      const htmlStr = `<span class="active">${source}</span>`

      // // 正则表达式
      // // 参数1：匹配的字符串
      // // 参数2：匹配规则，g全局，i不区分大小写
      const reg = new RegExp(source, 'gi')

      return text.replace(reg, htmlStr)
    }
  }
}
</script>

<style lang="less" scoped>
  .search-suggestion{
    /deep/span.active{
      color: #3296fa !important;
    }
  }
</style>
