<template>
  <div class="search-result">
    <van-list
  v-model="loading"
  :finished="finished"
  finished-text="没有更多了"
  :error.sync="error"
  error-text="请求失败，点击重新加载"
  @load="onLoad"
>
  <van-cell v-for="(item,index) in list" :key="index" :title="item.title" />
</van-list>
  </div>
</template>

<script>
import { getSearchResult } from '@/api/search.js'
export default {
  name: 'SearchResult',

  data () {
    return {
      list: [],
      loading: false,
      finished: false,

      error: false,

      page: 1,
      per_page: 20

    }
  },

  props: {
    searchText: {
      type: String,
      required: true
    }
  },

  mounted () {

  },

  methods: {
    async onLoad () {
      try {
        const { data: res } = await getSearchResult({
          page: this.page,
          per_page: this.per_page,
          q: this.searchText
        })

        console.log(res)

        this.list.push(...res.data.results)

        this.loading = false

        // 判断是否加载完成
        if (res.data.results.length) {
          this.page++
        } else {
          this.finished = true
        }
      } catch (err) {
        this.error = true
        this.loading = false
        this.$toast('获取搜索结果失败')
      }
    }

  }
}
</script>

